import { OrderModel } from "../../model/order"
const orderModel = new OrderModel();

export default {
    components:{
    },
    props:{
        carData:{
            type:Object,
            default:{},
            
        }
    },
    data() {
        const currentDate = this.getDate({
            format: true
        })
        return {
            title: 'picker',
            array: ['中国', '美国', '巴西', '日本'],
            index: 0,
            date: currentDate,
            time: '12:01',
            phone: '',
            shop:{}
        }
    },
    name: "car_inspection",
     computed: {
         startDate() {
             return this.getDate('start');
         },
         endDate() {
             return this.getDate('end');
         }
     },
    methods:{
        phoneChange(e) {
            var phone = e.detail.value;
            if(!(/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/.test(phone))){
                uni.showToast({
                    title:'手机号码有错，请重新输入',
                    icon:'none'
                });
                return false;
            }
            this.phone = phone;
        },
        bindDateChange: function(e) {
            this.date = e.target.value
        },
        bindTimeChange: function(e) {
            this.time = e.target.value
        },
        getDate(type) {
            const date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();

            if (type === 'start') {
                year = year - 60;
            } else if (type === 'end') {
                year = year + 2;
            }
            month = month > 9 ? month : '0' + month;;
            day = day > 9 ? day : '0' + day;
            return `${year}-${month}-${day}`;
        },
         _provider(){
           return new Promise((resolve,reject)=>{
                uni.getProvider({
                    service: 'payment',
                    success: function (res) {
                        console.log(res.provider[0])
                        resolve(res.provider[0])
                    }}
                )
            })

        },
        goCloseAccount() {
            let params = {
                car_num: this.carData.car.car_num,
                epuipmen_id: this.carData.car.equipmen_id,
                vehicle_id: this.carData.car.vehicle_id,
                phone: this.phone,
                appointment_date: this.date,
                appointment_time: this.time,
                review_station: this.shop.name,
                type : 7
            }
            orderModel.createOrder(params).then( res => {
                if (res.code == 200 || res.code == 201) {
                    let data = res.data
                    uni.navigateTo({
                        url:`/pagesB/pay/pay?type=commodity&data=` + JSON.stringify(data.internal_payment_sn)
                    })
                }
                
            } )
        },
        
    },
    mounted(){
        
        if (uni.getStorageSync('shop')) {
            console.log(JSON.parse(uni.getStorageSync('shop')))
            this.shop = JSON.parse(uni.getStorageSync('shop'))	
        }
    }
}
